import React from "react";
import Container from "react-bootstrap/esm/Container";
import Micro from "./Micro";

const PostIdPage = (props) => {
  let poem = {};
  const options = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ poemId: props.id }),
  };

  fetch("https://zoobrilka-alice-skill.herokuapp.com/api/getPoem", options)
    .then((response) => response.json())
    .then((response) => handleData(response));

  function handleData(data) {
    poem.author = data.author;
    poem.title = data.title;
    poem.text = data.text;
  }

  return (
    <Container>
      <div className="row my-5 ">
        <div
          className="col-5  border-bottom border-primary pb-5 mb-5"
          style={{ minWidth: "310px" }}
        >
          <div
            className="d-flex flex-column align-items-center"
            style={{ fontWeight: "500" }}
          >
            <h3 className="">{poem.title}</h3>
            <h4 className="mb-3">{poem.author}</h4>
            <p className="" style={{ whiteSpace: "pre-line" }}>
              {poem.text}
            </p>
          </div>
        </div>
        <div className="col">
          <Micro />
        </div>
      </div>
    </Container>
  );
};

export default PostIdPage;
