import React from "react";
import Container from "react-bootstrap/esm/Container";
import Micro from "./components/Micro";
function Example() {
  return (
    <Container>
      <div className="row my-5">
        <div
          className="col-5  border-bottom border-primary pb-5 mb-5"
          style={{ minWidth: "310px" }}
        >
          <div
            className="d-flex flex-column align-items-center"
            style={{ fontWeight: "500" }}
          >
            <h3 className="">Ты и Вы</h3>
            <h4 className="mb-3">Александр Сергеевич Пушкин</h4>
            <p className="">
              В те дни, когда мне были новы
              <br />
              Все впечатленья бытия –<br />
              И взоры дев, и шум дубровы,
              <br />
              И ночью пенье соловья, –<br />
              Когда возвышенные чувства,
              <br />
              Свобода, слава и любовь
              <br />
              И вдохновенные искусства
              <br />
              Так сильно волновали кровь, –<br />
              Часы надежд и наслаждений
              <br />
              Тоской внезапной осеня,
              <br />
              Тогда какой-то злобный гений
              <br />
              Стал тайно навещать меня.
            </p>
          </div>
        </div>
        <div className="col">
          <Micro></Micro>
        </div>
      </div>
    </Container>
  );
}
export default Example;
